.cta-container {
  position: relative;
  /* width: 100%;  */
  height: 550px;
  background: linear-gradient(180deg, #0E2F7E 0%, #1F1F1F 100%);
  overflow: hidden;
  border-radius: 20px;
}

/* Background image styling */
.background-image {
  /* position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); */
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  opacity: 10%;
}
  
/* Overlay styles */
.overlay {
  width: 80%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center; 
  color: #ffffff; /* Text color */
}
  
  /* Text styling */
.text {
  margin: 0 0 50px;
  font-size: 40px;
  font-weight: 700;
  line-height: 60px;
}

@media (max-width: 768px) {
  .text {
    font-size: 20px;
    line-height: 40px;
  }
  
}
  
/* Button styling */
.button {
    background-color: #f6530d;
    color: #ffffff;
    border: none;
    padding: 12px 36px;
    border-radius: 10px;
    cursor: pointer;
    font-family: Poppins;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
}
  
.button:hover {
    background-color: #e04a0c; /* Change color on hover */
}